* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-wrapper {
  background-color: #342b43;
  min-width: 450px;
  min-height: 650px;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 6px 40px #000;
  margin-bottom: 10px;
}

.header h1 {
  color: #fff;
  text-align: center;
  margin: 30px 0;
}

.tast-input {
  outline: none;
  width: 260px;
  padding: 15px;
  margin-right: 20px;
  font-size: 20px;
  color: #ccc;
  background-color: #000;
  border: 1px solid #53b3f7;
  border-radius: 10px;
}

.button-add {
  width: 80px;
  padding: 15px 15px;
  font-size: 20px;
  border-radius: 10px;
  border: 0;
  background-color: #53b3f7;
  cursor: pointer;
}

.list-item {
  display: flex;
  border: 1px solid #8a769d;
  border-radius: 10px;
  padding: 10px;
  min-height: 30px;
  margin-top: 20px;
}

.button-complete,
.button-delete,
.button-edit {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;
}

.button-complete {
  color: #0b5b1d;
  margin-right: 10px;
}

.button-edit {
  color: #2ae7ce;
  margin-right: 10px;
}

.button-delete {
  color: #f32983;
}

.complete {
  text-decoration-style: solid;
  text-decoration-line: line-through;
  text-decoration-color: #682c2c;
  opacity: 0.6;
}

.list {
  width: 220px;
  background-color: transparent;
  border: none;
  color: #ccc;
  font-size: 20px;
  padding-left: 10px;
  margin-right: 15px;
}

input:focus,
button:focus {
  outline: none;
}

@media (max-width: 470px) {
  .app-wrapper {
    margin: 30px;
    min-width: auto;
    min-height: auto;
  }
  .tast-input {
    width: calc(100% - 30px);
  }

  .button-add {
    width: 100%;
    margin-top: 10px;
  }

  .list {
    text-align: center;
  }

  .list-item {
    flex-wrap: wrap;
    justify-content: center;
  }

  .list-item div {
    margin-top: 10px;
  }
}

@media (max-width: 360px) {
  .container {
    display: block;
    min-height: auto;
  }
}
